.cl {
    &--darkgray {
        color: $darkGray;
        fill: $darkGray;
        @include heading {
            color: $darkGray; } }
    &--gray {
        color: $gray;
        fill: $gray;
        @include heading {
            color: $gray; } }
    &--gains {
        color: $gainsboro;
        fill: $gainsboro;
        @include heading {
            color: $gainsboro; } }
    &--black {
        color: $black;
        fill: $black;
        @include heading {
            color: $black; } }
    &--green {
        color: limegreen;
        fill: limegreen;
        @include heading {
            color: limegreen; } }
    &--white {
        color: $white;
        fill: $white;
        @include heading {
            color: $white;
            fill: $white; } }
    &--silver {
        color: $silver;
        @include heading {
            color: $silver; } }
    &--blue {
        color: $blueBox;
        @include heading {
            color: $blueBox; } }
    &--red {
        color: $redBox;
        @include heading {
            color: $redBox; } }
    &--lightBlue {
        color: $blueLightBox;
        @include heading {
            color: $blueLightBox; } } }
