// list.sass
.list {
    &--inline {
        padding-right: 0px;
        padding-left: 0px;
        margin-left: 0px;
        list-style: none;
        vertical-align: middle;
        position: relative;
        &>li {
            display: inline-block;
            padding-left: 0px;
            padding-right: 0px;
            vertical-align: middle;
            position: relative;
            z-index: 19;
            @include MQMAX(S) {
                padding-left: 10px;
                padding-right: 10px; } } } }
// SITE ITEM
.list {
    &_two_item {
        li:first-child {
            margin-right: 5px; }
        li:last-child {
            margin-left: 5px; } }
    &_site {
        li {
            position: relative;
            padding-left: 35px;
            padding-bottom: 5px; }
        li:after {
            position: absolute;
            top: -30px;
            left: 0px;
            color: $blue;
            font-size: 55px;
            font-weight: 900;
            content: "-"; } }
    &_contact {
        display: block;
        table-layout: fixed;
        width: 100%;
        padding: 2.5rem 0;
        @include MQ(M) {
            display: table; }
        li {
            display: block;
            padding: 0px;
            text-align: center;
            @include MQ(M) {
                display: table-cell; } } } }
