//Clen Boost
.btn {
    &-link {
        text-decoration: none !important; } }

// buttons
.btn {
    box-shadow: none;
    font-size: 1.4rem;
    font-family: $primary-font;
    padding: 0.8em 1.7em;
    cursor: pointer;
    font-weight: 600;
    -webkit-appearance: inherit;
    transition: all 0.3s ease-in-out;
    &_menu {
        padding: .25rem 2.5rem .25rem .25rem;
        font-size: 1.25rem;
        line-height: 1;
        background-color: rgba($white, .5);
        border: transparent;
        width: auto;
        height: auto;
        cursor: pointer;
        position: fixed;
        z-index: 5;
        vertical-align: middle;
        text-decoration: none;
        text-underline: none;
        transform: $trs-site;
        text-shadow: 0 0 1px black;
        @include press {
            text-underline: none;
            text-decoration: none;
            outline: none; }
        @include MQ(M) {
            background-color: transparent;
            color: $black;
            fill: $black; }
        span {
            vertical-align: middle; } }
    &_submit {
        width: 100%;
        color: white;
        background-color: $blue;
        border-bottom: 5px solid transparent;
        @include press {
            color: white;
            border-bottom: 5px solid $lightBlue; } }
    &_ghost {
        @include btnGhost; }
    &-box {
        z-index: 12;
        margin-top: 0px;
        display: none;
        width: 70%;
        position: relative;
        @include MQ(L) {
            display: inline-block;
            width: auto; } }
    &_full {
        width: 100%;
        &--lg {
            @include MQMAX(MXL) {
                width: 100%;
                display: block;
                margin-bottom: 5px; } } }
    &_radio {
        border-radius: 22px; }
    &_quote {
        position: relative;
        line-height: 24px;
        color: $white;
        font-size: 15px;
        font-weight: 700;
        padding: 8px 30px;
        background-color: $fire;
        text-transform: uppercase;
        border: 2px solid transparent;
        top: 8px;
        @include press {
            background-color: $firedark;
            color: $white; }
        @include MQ(M) {
            top: inherit;
            padding: 11px 52px; } } }
//Colors BTN Site
.btn {
    &_site {
        background-color: $red;
        color: $white;
        @include press {
            background-color: $fire;
            color: $white; }
        span.icon {
            &:after {
                content: "+";
                margin-right: 12px; } }
        &--gray {
            background-color: silver;
            color: $white; }

        &--white {
            background-color: $white;
            @include press {
                background-color: $white;
                box-shadow: 0px 5px 5px 0px rgba($black, 0.3); }
            &.cl--blue {
                @include press {
                    color: $blueBox; } } } } }
// Outline Color BTN SITe
.btn {
    &_outline {
        &_white {
            color: $white;
            background-color: transparent;
            background-image: none;
            border-color: $white;
            @include press {
                color: $white;
                text-shadow: 0px 2px 2px rgba($black, 0.31);
                box-shadow: 0px 5px 5px 0px rgba($black, 0.3); } }
        &_gray {
            color: $gray;
            background-color: transparent;
            background-image: none;
            border-color: $darkGray; } } }
.disabled {
    cursor: no-drop;
    @include press {
        color: $silver;
        p {
            color: $silver; } } }
