// text.sass
.title,
.subtitle,
.intro,
.action,
.paragraf {
    display: block;
    position: relative; }
.paragraf {
    &_em2 {
        p {
            @include font-size(1.5); } } }

//Deco txt
.subtitle_line:after,
.title_line:after {
    content: "";
    display: inline-block;
    text-align: center;
    background: $gray;
    width: 7%;
    height: 2px; }
.title_line--gains:after {
    background: $dimGray; }
//.tag_data
.txt {
    &_two {
        font-family: $second-font; }
    &_semibold {
        font-weight: 400; }
    &_bold {
        font-weight: bold; }
    &_weight {
        &900 {
            font-weight: 900;
            @include header {
                font-weight: 900; }
            p {
                font-weight: 900; } } }
    &_italic {
        font-style: italic; }

    &_center {
        text-align: center;
        &--movil {
            @include MQMAX(M) {
                text-align: center; } } }
    &_upper {
        text-transform: uppercase; }
    &_capital {
        text-transform: capitalize; }
    &_right {
        text-align: right; }
    &_shadow {
        text-shadow: 0px 2px 2px ($black); }
    &_size {
        &--em1 {
            @include font-size(1.5);
            line-height: 1.1;
            li,
            p,
            b,
            span {
                @include font-size(1.5);
                line-height: 1.1; }
            @include heading {
                @include font-size(1.5);
                line-height: 1.1; } }
        &--em2 {
            @include font-size(2);
            line-height: 1.1;
            li,
            p,
            b,
            span {
                @include font-size(2);
                line-height: 1.1; }
            @include heading {
                @include font-size(2);
                line-height: 1.1; }
            &a {
                @include font-size(2.5);
                line-height: 1.1;
                li,
                p,
                b,
                span {
                    @include font-size(2.5);
                    line-height: 1.1; }
                @include heading {
                    @include font-size(2.5);
                    line-height: 1.1; } } }
        &--em3 {
            @include font-size(3);
            line-height: 1.1;
            li,
            p,
            b,
            span {
                @include font-size(3);
                line-height: 1.1; }
            @include heading {
                @include font-size(3);
                line-height: 1.1; } }
        &--em4 {
            @include font-size(4);
            line-height: 1.1;
            li,
            p,
            b,
            span {
                @include font-size(4);
                line-height: 1.1; }
            @include heading {
                @include font-size(4);
                line-height: 1.1; } }
        &--em5 {
            @include font-size(5);
            line-height: 1.1;
            li,
            p,
            b,
            span {
                @include font-size(5);
                line-height: 1.1; }
            @include heading {
                @include font-size(5);
                line-height: 1.1; } }
        &--em6 {
            @include font-size(7);
            line-height: 1.1;
            li,
            p,
            b,
            span {
                @include font-size(7);
                line-height: 1.1; }
            @include heading {
                @include font-size(7);
                line-height: 1.1; } } }
    &_completed {
        text-decoration: line-through;
        font-style: italic;
        color: $gainsboro; } }
.completado {
    text-decoration: line-through;
    font-style: italic;
    color: $gainsboro; }

.title {
    &_site {
        @include header {
            position: relative;
            display: inline-block;
            padding-bottom: 18px;
            &:after {
                position: absolute;
                content: '';
                left: 0px;
                bottom: 0px;
                width: 50px;
                height: 10px;
                background-color: $red;
                border-bottom: 5px solid $black; } }
        &.txt_center {
            @include header {
                &:after {
                    left: 50%;
                    margin-left: -25px; } } } } }
