// box
.box {
    &_shadow {
        box-shadow: 0px 10px 34px -5px rgba($black, .05); }
    &_form {
        display: inline-block;
        position: relative;
        border-radius: 26px;
        &_title {
            display: inline-block;
            position: relative;
            border-top-left-radius: 26px;
            border-top-right-radius: 26px;
            padding: 1.8rem 1.8rem;
            width: 100%; } } }
.float_img {
    //z-index: 99
    &:after {
        content: "";
        //display: inline-block
        position: absolute;
        bottom: 0;
        left: -343px;
        background: transparent url("/../images/float_render.png")no-repeat center center/contain;
        background: transparent url("./../images/float_render.png")no-repeat center center/contain;
        width: 363px;
        height: 153px;
        z-index: 1; } }
.form-horizontal {
    z-index: 99; }
.deco {
    &:after {
        content: "";
        display: inline-block;
        width: 100%;
        background: transparent url('/../images/deco.png')no-repeat bottom center/106%;
        background: transparent url('./../images/deco.png')no-repeat bottom center/106%;
        height: 65px;
        @include MQ(S) {
            height: 121px; }
        @include MQ(M) {
            height: 51px; }
        @include MQ(L) {
            height: 65px; } } }
